/* Imports Google Font Poppins */
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

/* Imports Google Font Roboto Mono */
@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,100..700;1,100..700&display=swap');

/* Default element styles*/
* {
    font-family: 'Poppins', 'Arial';
    margin: 0;
    padding: 0;
}

html {
    /* scroll-behavior: smooth; */
    background-color: #121212;
}

section {
    background-color: #121212;
}

/* Navbar styles */
.navbar {
    background-color: #121212;
    font-size: 18px;
    color: rgba(255, 255, 255, 1);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 120px;
    padding: 0 72px;
}

.banner-link {
    color: rgba(255, 255, 255, 1);
    font-size: 24px;
    font-weight: 600;
    text-decoration: none;
}

.primary-links {
    display: flex;
    gap: 48px;
    align-items: center;
}

.nav-link {
    color: rgba(255, 255, 255, .7);
    text-decoration: none;
    font-weight: 600;
    position: relative;
    transition: ease-out .5s;
    justify-content: center;
}

.nav-link::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 2px;
    background-image: linear-gradient(to right, #4285F4, #D6C8FF);
    opacity: 0;
    transition: ease-out .5s;
}

.nav-link:hover::after {
    opacity: 1;
}

.nav-link:hover {
    color: rgba(255, 255, 255, 1);
    transition: ease-out .5s;
}

.nav-link.active::after,
.nav-link:focus::after {
    opacity: 1;
}

.nav-link.active,
.nav-link:focus {
    color: rgba(255, 255, 255, 1);
}

/* Button Styles */
.primary-button {
    display: flex;
    border: 1px solid rgba(255, 255, 255, 1);
    color: rgba(255, 255, 255, 1);
    font-size: 18px;
    padding: 24px 32px;
    transition: ease .5s;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    text-decoration: none;
}

.primary-button:hover {
    background-color: rgba(255, 255, 255, 1);
    color: #121212;
}

.nav-button-mods {
    padding: 16px 20px;
}

.secondary-button {
    display: flex;
    border: 1px transparent;
    color: rgba(255, 255, 255, 1);
    font-size: 18px;
    padding: 24px 32px;
    transition: ease .5s;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    text-decoration: none;
}

.secondary-button:hover {
    text-decoration: underline;
}

.button-container {
    display: flex;
    flex-direction: row;
    gap: 20px;
}


/* Hero Section Styles */
.hero-section {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 32px;
    padding: 124px 0 0 0;
}

.hero-subsection {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 56px;
}

h1 {
    font-size: 96px;
    font-weight: 600;
    color: rgba(255, 255, 255, 1);
}

.text-gradient {
    background-image: linear-gradient(to right, #4285F4, #D6C8FF);
    /* Gradient as background */
    background-clip: text;
    background-size: 100%;
    -webkit-text-fill-color: transparent;
    font-weight: 700;
}

.spacer {
    margin-left: 10px;
}

.spacer-both {
    margin-left: 10px;
    padding-right: 10px;
}

/* Hero section subtitle */
.sub-title {
    font-size: 32px;
    color: rgba(255, 255, 255, 1);
    font-weight: 600;
    text-align: center;
}

.prob-svg {
    border: 4px solid;
    background-image: linear-gradient(to right, #4285F4, #D6C8FF);
    border-radius: 10px;
    border-image: linear-gradient(45deg, #4285F4, #D6C8FF) 1;
    margin: 64px 0;
    box-shadow: 0px 0px 80px rgba(35, 43, 67, 1);
}

/* Challenges Section Styles */

.page-section {
    padding: 180px 72px 32px 72px;
    background-color: #121212;
}

h2 {
    font-size: 48px;
    color: rgba(255, 255, 255, 1);
    font-weight: 600;
    display: flex;
    justify-content: center;
}

.challenge-container {
    display: flex;
    flex-direction: row;
    gap: 80px;
    justify-content: center;
    padding: 180px 0 0 0;
}

.challenge-item {
    width: 272px;
    height: 272px;
    border: 1px solid rgba(255, 255, 255, .7);
    background-image: linear-gradient(to right, #121212, #2A3558);
    transform: rotate(45deg);
    color: rgba(255, 255, 255, 1);
    position: relative;
    text-align: center;
}

.challenge-num {
    font-family: 'Roboto Mono';
    font-size: 18px;
    font-weight: 800;
}

.challenge-desc {
    font-size: 24px;
    font-weight: 600;
    padding: 48px 0;
    width: 280px;
}

.challenge-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(-45deg);
    display: flex;
    flex-direction: column;
    align-items: center;
}

/* Features Section Styles */
.heading-caption {
    font-size: 24px;
    font-weight: 400;
    color: rgba(255, 255, 255, .7);
    text-align: center;
    padding: 32px 0;
}

.features-box {
    padding: 32px 72px;
    background-color: #121212;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.feature-container {
    display: flex;
    flex-direction: row;
    border-radius: 16px;
    background-color: #2A3558;
    /* gap: 64px; */
    padding: 32px 32px;
    height: 480px;
    overflow: hidden;
}

.feature-container-gradient {
    background-image: linear-gradient(to right, #121212, #2A3558);
}

.feature-container-two {
    display: flex;
    flex: 1;
    flex-direction: column;
    gap: 64px;
    background-color: #2A3558;
    border-radius: 16px;
    /* gap: 64px; */
    padding: 32px 32px 0px;
    height: 780px;
    overflow: hidden;
    margin-right: 20px;
    /* height: auto; */
}

.feature-container-two:last-child {
    margin-right: 0;
}

.feature-content {
    display: flex;
    flex-direction: column;
    gap: 8px;
    flex: 1;
}

.feature-num {
    font-family: 'Roboto Mono';
    font-size: 18px;
    font-weight: 400;
    color: rgba(255, 255, 255, .7);
}

.feature-desc {
    font-size: 18px;
    font-weight: 400;
    color: rgba(255, 255, 255, .7);
}

.feature-img {
    flex: 1;
    position: relative;
    right: -100px;
}

.feature-four-img {
    position: relative;
    right: -100px;
    bottom: -28px;
}

.feature-five-img {
    position: relative;
    bottom: 40px;
}

.feature-six-img {
    position: relative;
    left: -20px;
}

h3 {
    font-size: 32px;
    font-weight: 600;
    color: rgba(255, 255, 255, 1);
}

.two-features {
    display: flex;
}

.three-features {
    display: flex;
}

.feature-container-three {
    display: flex;
    flex: 1;
    flex-direction: column;
    align-items: center;
    background-color: #2A3558;
    border-radius: 16px;
    padding: 32px 32px 0px;
    height: 540px;
    overflow: hidden;
    margin-right: 20px;
    /* height: auto; */
}

.feature-container-three:last-child {
    margin-right: 0;
}

.feature-img-center {
    right: 0px;
    bottom: -10px;
    width: 99%;
    border-radius: 4px;
    border: 1px solid #898888;
}


/* Demo Video Section Styles */

.demo-container {
    display: flex;
    height: 80vh;
    padding: 0 72px;
}

.demo-vid {
    width: 100%;
    height: 100%;
    border: 0px solid transparent;
    border-radius: 10px;
    margin: 64px 0;
    box-shadow: 0px 0px 80px rgba(35, 43, 67, 1);
}

/* Testing Table Styles */
.table-container {
    display: flex;
    flex-direction: column;
    gap: 48px;
    padding-top: 72px;
    padding-left: 72px;
    padding-right: 72px;
}

.table-row {
    display: flex;
    align-items: flex-start;
    gap: 140px;
}

.row-desc {
    font-size: 18px;
    color: rgba(255, 255, 255, .7);
    font-weight: 300;
    flex: 2;
}

.row-tester {
    font-family: 'Roboto Mono';
    font-size: 18px;
    font-weight: 800;
    color: rgba(255, 255, 255, 1);
    flex: 1;
}

.row-tester-human {
    font-family: 'Roboto Mono';
    font-size: 18px;
    font-weight: 300;
    color: rgba(255, 255, 255, 1);
    flex: 1;

}

.table-line {
    border-color: rgba(255, 255, 255, .6);
    width: 100%;
}

.row-title {
    font-size: 18px;
    color: rgba(255, 255, 255, 1);
    font-weight: 600;
    flex: 1;
    align-content: flex-start;
}

.col-heading {
    font-family: 'Roboto Mono';
    font-size: 18px;
    font-weight: 800;
    color: rgba(255, 255, 255, .7);
    flex: 1;
}

.col-heading-desc {
    font-family: 'Roboto Mono';
    font-size: 18px;
    font-weight: 800;
    color: rgba(255, 255, 255, .7);
    flex: 2;
}

.col-heading-vedette {
    font-family: 'Roboto Mono';
    font-size: 18px;
    font-weight: 800;
    background-image: linear-gradient(to right, #4285F4, #D6C8FF);
    /* Gradient as background */
    background-clip: text;
    background-size: 100%;
    -webkit-text-fill-color: transparent;
    flex: 1;
}

.help-icon:hover {
    cursor: default;
    background-image: linear-gradient(to right, #4285F4, #D6C8FF);
    background-clip: text;
    background-size: 100%;
    -webkit-text-fill-color: transparent;
}

/* FAQs Section Styles */
.faqs {
    min-height: 100vh;
    max-height: 300vh;
    padding: 56px 300px 0px 300px;
    align-content: center;
    padding-top: 180px;
}

/* Modifed from https://github.com/ajaymuktha/React-Accordion/tree/main */
.faqs h3 {
    text-align: center;
    font-size: 48px;
    color: rgba(255, 255, 255, 1);
    margin-bottom: 32px;
}

.question {
    padding: 4vh 4vh;
    margin-bottom: 2vh;
    border-radius: 16px;
    color: rgba(255, 255, 255, 1);
    background-image: linear-gradient(to right, #2F3F68, #1E2A4B);
    cursor: pointer;
}

.question:hover {
    background-image: linear-gradient(to right, #2F3F68, #2F3F68);
}

.question p {
    color: rgba(255, 255, 255, 1);
    margin-bottom: 0;
