}

.question header h4 {
    margin-bottom: 0;
}

.btn {
    color: rgba(255, 255, 255, 1);
    width: 2vh;
    height: 2vh;
    display: flex;
    align-items: center;
    justify-content: center;
    align-self: center;
    background: transparent;
    border-color: transparent;
