/* Milestone Page Styles */

/* Milestone Cards */

.milestones-parent-conainter {
    display: flex;
    flex-direction: column;
    gap: 24px;
    padding-top: 56px;
}

.milestone-container {
    display: flex;
    justify-content: center;
    gap: 24px;
}

.milestone-card {
    background-image: linear-gradient(to right, #2F3F68, #1E2A4B);
    display: flex;
    flex-direction: column;
    /* flex: 1; */
    border-radius: 16px;
    gap: 8px;
    justify-content: center;
    align-items: center;
    width: 320px;
    height: 320px;
}

.milestone-value {
