}

.features-box.milestone {
    padding-top: 56px;
}

.three-features.milestone {
    padding-top: 56px;
}

.next-step-img-left {
    position: relative;
    left: -160px;
    bottom: -24px;
}

.next-step-img-center {
    position: relative;
    bottom: -24px;
}

.next-step-img-right {
    position: relative;
    right: -160px;
    bottom: -24px;
}

.page-section.bottom {
    padding-bottom: 120px;
}

/* Footer */
.footer {
    padding: 0 72px;
    background-color: #121212;
}

.footer-container {
    font-size: 18px;
    color: rgba(255, 255, 255, 1);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 120px;
    border-top: 2px solid;
    border-image: linear-gradient(to right, #A3B5FC, #D8CDff);
    border-image-slice: 1;
}

.footer-link {
    color: rgba(255, 255, 255, 1);
    text-decoration: none;
    font-weight: 600;
    position: relative;
    transition: ease-out .5s;
    justify-content: center;
}

.footer-link:hover {
    color: rgba(255, 255, 255, .7);
}



