    html {
        /* scroll-behavior: smooth; */
        margin: 0px 280px;
           /* padding: 5% 20%; */
    }
    
    .page-section {
        /* padding: 160px 360px; */
        /* padding: 5% 20%; */
        /* margin: 80px 280px; */
    }

    .threecircles-img-right {
        right: -550px;
        bottom: 240px;
    }

    .aiglitter-img-right {
        right: -240px;
        bottom: 8px;
    }

    .google-img-right {
        right: -240px;
        bottom: 40px;
    }

    .faqs {
        padding: 0 20%;
        padding-top: 180px;
    }
}